import { Component } from "react"
import styled from "styled-components"
import { ApiGeneric } from "src/services/apiServices"
import { HUtils, MyUI } from "@macashipo/mlib"
import { MyModal } from "src/core/components"

class MPDailyTask extends Component {
  constructor(props) {
    super(props)
    this.state = {}
    this._params = props?.match?.params
    this._taskid = this._params["taskid"]
  }
  componentDidMount() {
    this.requestOptions()
  }
  requestOptions = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "/api/v1/UserTask",
            name: "Options",
          },
          data: {},
          successCallBack: response => {
            if (response.Data) {
              this.setState(
                {
                  options: response.Data,
                  loading: false,
                },
                () => {
                  this.requestTasks()
                }
              )
            }
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }
  requestTasks = () => {
    this.setState(
      {
        loading: true,
      },
      () => {
        ApiGeneric({
          request: {
            method: "POST",
            path: "/api/v1/UserTask/",
            name: "Get",
          },
          data: {},
          successCallBack: response => {
            if (response.Data.Data) {
              this.setState({
                tasks: response.Data.Data,
                loading: false,
              })
            }
          },
          errorCallBack: err => {
            console.log(err)
            this.setState({
              loading: false,
            })
          },
        })
      }
    )
  }
  render() {
    const { tasks } = this.state
    return (
      <WrapLayout>
        <div className="a-header">
          <div className="a-left">
            <div className="a-title">Task</div>
          </div>
          <div className="a-right">
            <button
              className="btn btn-primary"
              onClick={() => {
                window.location.href = "/dailytasks"
              }}
            >
              My Tasks
            </button>
            <button
              className="btn btn-primary"
              onClick={() => {
                MyModal.showFromMyUI(
                  {
                    type: "prj_dailytask",
                  },
                  {
                    title: "Report Task",
                    size: "xl",
                    backdrop: "static",
                  }
                )
              }}
            >
              Report Task
            </button>
          </div>
        </div>
        <div className="a-body">Task</div>
      </WrapLayout>
    )
  }
}

const WrapLayout = styled.div`
  & > .a-header {
    display: flex;
    border-bottom: 1px solid #1c4e7e;
    margin-bottom: 10px;
    padding-bottom: 10px;
    & > .a-left {
      & > .a-title {
        font-size: 20px;
      }
    }
    & > .a-right {
      margin-left: auto;
      & > * {
        margin-left: 2px;
      }
    }
  }
  & > .a-body {
  }
`

export default MPDailyTask
